.post {
  position: relative;
  width: 100%;
  padding-bottom: 40px;
  margin-bottom: 40px;
  background: url("../img/bg-grey.png") repeat-x bottom;

  &__icon {
    position: absolute;
    left: -85px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 75px;
    height: 75px;
    font-size: 28px;
    background-color: $red;
    svg {
      fill: #fff; } }

  &__media {
    width: 100%;
    margin-bottom: 20px;
    img {
      width: 100%; } }

  &__content {
    display: flex; }

  &__title {
    margin-bottom: 2px;
    @include bebas;
    font-size: 18px;
    line-height: 1;
    color: $red;
    text-transform: uppercase; }

  &__text {
    margin-top: 20px;
    p {
      margin-bottom: 20px; } }

  &__link-text {
    color: #a0a0a0; }

  &__quotes {
    padding: 20px;
    padding-left: 70px;
    background: whitesmoke url("../img/bg-quotes.png") no-repeat 30px 20px; } }
